<template>
  <div class="user-wrap">
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <p class="connect">
      联系方式
    </p>
    <p class="connect">
      联系电话：{{tel}}
    </p>
    <p class="connect">
      微信客服：{{wechat}}
    </p>
    <p class="connect">
      工作时间：周一到周日：8:00-20:00
    </p>
    <img 
      class="qscode-img"
      v-for="(img, index) in imageList" 
      :key="index" 
      :src="img"
    />
  </div>
</template>

<script>
export default {
  name: 'questions',
  components: {
  },
  data () {
    return {
      title: this.$route.query.type === 1 ? '联系客服' : '商务合作',
      imageList: [
        'https://img.yzcdn.cn/vant/apple-1.jpg'
      ],
      tel: '1231231231',
      wechat: '2131231232'
    }
  },
  computed: {
  },
  mounted () {
  },
  methods: {
    onClickLeft () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped lang="less">
  .qscode-img {
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 6px;
    margin: 20px 4% 0;
    box-sizing: border-box;
    width: 92%;
  }
  .connect {
    padding: 10px 15px 0;
    font-size: 14px;
  }
</style>


